import Lines from '@/components/common/Lines.vue'
import CommodityList from './CommodityList.vue'
import $http from '@/common/api/request.js'
export default {
	props: {
		keyword: String
	},
	data() {
		return {
			shopList: {
				currentIndex: 0,
				data: [{
						name: "价格",
						status: 1,
						key: "pprice"
					},
					{
						name: "折扣",
						status: 0,
						key: "discount"
					},
					{
						name: "品牌",
						status: 0
					}
				]
			},
			dataList: []
		}
	},
	components: {
		Lines,
		CommodityList
	},
	computed: {
		orderBy() {
			//拿到当前对象
			let obj = this.shopList.data[this.shopList.currentIndex];
			let val = obj.status === "1" ? "asc" : "desc";
			return {
				[obj.key]: val
			}
		}
	},
	mounted() {
		this.getData();
	},
	methods: {
		//请求搜索数据
		getData() {
			$http.request({
				url: "/goods/search",
				data: {
					name: this.keyword,
					...this.orderBy
				}
			}).then((res) => {
				this.dataList = res;
			}).catch(() => {
				uni.showToast({
					title: '请求失败',
					icon: 'none'
				})
			})
		},
		changeTab(index) {
			//点击排序
			this.getData();
			//箭头索引值
			let idxs = this.shopList.currentIndex;
			//具体哪个对象
			let item = this.shopList.data[idxs];
			if (idxs == index) {
				return item.status = item.status === 1 ? 2 : 1;
			}
			let newItem = this.shopList.data[index];
			item.status = 0;
			this.shopList.currentIndex = index;
			newItem.status = 1;
		}
	}
}
